import React from 'react';

const Service2 = () => {
  return (

    <div class='bac_services service2'>
        <div class="left-side service-child padding_left_el">
        <div class="service-title"><h2>Rapports</h2></div>
        <div className='back_service_overview'>
          <p>
          Nous vous proposons un service sur mesure pour prendre en charge toutes vos tches de traitement de texte. 
          Que ce soit pour la saisie de donnes, 
          la mise en page de rapports, 
          la cration de tableaux ou la conversion de documents, 
          je m'adapte  vos besoins spcifiques
          </p>
        </div>
            
            <div class="service-text-content">
            <ul>
              <li>Saisie de documents
                <div className='service_description'>
                  <ul>
                    <li>Numrisation de documents manuscrits ou imprims</li>
                    <li>Saisie de donnes dans diffrents formats (Word, Excel, PDF, etc.)</li>
                    <li>Cration de tableaux et de graphiques</li>
                  </ul>
                </div>
              </li>
              <li>Mise en forme
              <div className='service_description'>
              <ul>
                    <li>Application de styles et de mises en page personnalises</li>
                    <li>Insertion d'images, de tableaux et de graphiques</li>
                    <li>Vrification de l'orthographe et de la grammaire</li>
                  </ul>
              </div>
              </li>
              <li>Traitement de donnes
              <div className='service_description'>
              <ul>
                    <li>Tri et organisation de donnes</li>
                    <li>Extraction de donnes</li>
                    <li>Cration de bases de donnes</li>
              </ul>
              </div>
              </li>

            </ul>
            </div>
        </div>
        <div class="left-side service-child saisie_rapport">
        {/* <img src="/images/services2/Reliure-dos-carr-coll-cristal.webp" alt="Portfolio" style={{ width: '100%' }} className="w3-hover-opacity" /> */}
        </div>
    </div>
  );
};

export default Service2;
